/* Reset styles */
body {
    margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
}

p:last-child {
    margin-bottom: 0;
}

ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

a {
    color: currentColor;
    text-decoration: none;
    display: block;
}

button {
    cursor: pointer;
}

img {
    display: block;
    width: auto;
    height: auto;
}

body.modal-open {
    overflow: hidden;
}