.shoppingList-item {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  margin-bottom: 20px;
  padding: 14px;
  border: 2px solid rgba(79, 46, 232, 0.4);
  border-radius: 16px;
  min-width: 335px;
  transition: transform 0.3s, box-shadow 0.3s;
}

.shoppingList-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 0 10px #0000004d;
}

@media (min-width: 768px) {
  .shoppingList-item {
    max-width: 704px;
    padding: 24px;
  }
}

@media (min-width: 1392px) {
  .shoppingList-item {
    max-width: 996px;
  }
}

.shoppingList-img {
  width: 100px;
  height: 142px;
  margin-right: 14px;
}

@media (min-width: 768px) {
  .shoppingList-img {
    width: 116px;
    height: 165px;
    margin-right: 24px;
  }
}

.shoppingList-trash-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0px;
  cursor: pointer;
  border: none;
  border-radius: 50%;
  background-color: transparent;
}

.shoppingList-icon-trash {
  height: 28px;
  width: 28px;
}

@media (min-width: 768px) {
  .shoppingList-icon-trash {
    height: 34px;
    width: 34px;
  }
}

.box-shoppingList {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

