body, h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

p:last-child {
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
  display: block;
}

button {
  cursor: pointer;
}

img {
  width: auto;
  height: auto;
  display: block;
}

body.modal-open {
  overflow: hidden;
}

:root {
  --main-backgraund-color: #f3f3f3;
  --main-white-color: #fff;
  --main-black-color: #111;
  --secondary-black-color: #202024;
  --blue-color: #4f2ee8;
  --yellow-color: #eac645;
  --cubic: cubic-bezier(.53, .36, .67, 1.07);
}

body {
  background-color: var(--body-bg);
  color: var(--main-black-color);
  font-family: DM Sans;
}

.dark-theme {
  background-color: var(--secondary-black-color);
  color: #b4afaf;
}

span {
  color: var(--blue-color);
}

.container {
  max-width: 335px;
  margin-left: auto;
  margin-right: auto;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 704px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    max-width: 1392px;
    overflow: visible;
  }
}

.visually-hidden {
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.side-bar {
  flex-direction: column;
  align-items: center;
  gap: 86px;
  margin-top: 40px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .side-bar {
    flex-direction: row;
    justify-content: space-between;
    gap: 32px;
  }
}

@media screen and (min-width: 1440px) {
  .side-bar {
    flex-direction: column;
    justify-content: start;
    margin-right: 40px;
  }

  .categories-wrapper {
    flex-direction: row;
    display: flex;
  }
}

.support-title {
  color: var(--main-white-color);
  letter-spacing: -1.12px;
  text-align: center;
  padding-top: 24px;
  font-size: 28px;
  line-height: 1.14;
}

.support-box {
  background: radial-gradient(189.32% 190.93% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
  border-radius: 16px;
  width: 335px;
  height: 352px;
  position: relative;
  box-shadow: 0 10px 10px #f5f5f71a;
}

@media screen and (min-width: 768px) {
