}

.support-list {
  flex-direction: column;
  gap: 20px;
  margin-left: 40px;
  display: flex;
}

.sup-container {
  height: 188px;
  margin-top: 40px;
  position: relative;
  overflow: hidden;
}

@media screen and (min-width: 768px) {
  .sup-container {
    height: 292px;
  }
}

.supp-btn {
  cursor: pointer;
  transition: background-color .3s var(--cubic), stroke .3s var(--cubic), border .3s var(--cubic);
  border: none;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  position: absolute;
  top: 300px;
  left: 50%;
  transform: translateX(-50%);
}

.supp-btn:hover, .supp-btn:focus {
  background-color: var(--blue-color);
  fill: var(--main-white-color);
  border: 2px solid #4f2ee84d;
}

@media screen and (min-width: 768px) {
  .supp-btn {
    width: 38px;
    height: 38px;
    top: 410px;
  }
}

.supp-svg {
  width: 100%;
  height: 100%;
}

@keyframes slideDown {
  0% {
    transform: translateY(-100%);
  }

  100% {
    transform: translateY(0);
  }
}

#support-list li {
  opacity: 0;
  animation: .3s ease-in-out forwards slideDown;
}

#support-list li:nth-child(-n+4) {
  opacity: 1;
}

#support-list li.hidden {
  display: none;
}

@media screen and (min-width: 768px) {
  #support-list li {
    opacity: 0;
    animation: .3s ease-in-out forwards slideDown;
  }

  #support-list li:nth-child(n+5) {
    opacity: 1;
  }

  #support-list li.hidden {
    display: none;
  }
}

.ukr-symbol {
