  padding-top: 5px;
  padding-bottom: 5px;
}

.support_a img {
  width: 130px;
  height: 32px;
  padding-left: 10px;
  position: absolute;
  left: 10%;
}

.support_a {
  filter: brightness(0) invert();
  transition: filter .3s var(--cubic);
  justify-content: start;
  align-items: center;
  padding-top: 5px;
  padding-bottom: 5px;
  display: flex;
  position: relative;
}

.support_a:hover, .support_a:focus {
